*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
span{
    color:orange;
}
.container6{
    display: flex;
    flex-direction: row;
}
.left-side{
    flex: 0.5;
    height: 100vh;
    display: flex;
    background-color: rgb(202, 202, 202);
    opacity: 0.9;
}
.h1{
    font-size:4em;
    margin-left:2em;
    margin-top:4.5em;
    font-weight:bold;
}
.right-side{
    border:1px solid grey;
    width:40%;
    height:100vh;
    border-radius:2px;
    flex: 0.5;
}
.h3{
    margin-left:50%;
    font-weight:bold;
    font-size:1.5em;
}
.content{
    padding:5px;
}
.label{
    margin-left:50px;
    font-weight:bold;
    font-size:16px
    /* padding:20px; */
} 
.input{
    margin-left:50px;
    outline:none;
    border-bottom:1px solid gray;
    width:70%;
    padding-top:20px;
}
.input::placeholder{
    font-size:14px;
}
.add-client{
    background-color:orange;
    margin-left:300px;
    color:white;
    width:100px;
    border-radius:5px;
    padding:5px;
    margin-bottom:10px;
}